import { Component, ElementRef, EventEmitter, forwardRef, Inject, Injector, NgModule, Optional, Renderer2, ViewEncapsulation } from '@angular/core';
import { CommonModule } from '@angular/common';

import { Util } from '../../../util/util';
import { OSharedModule } from '../../../shared';
import { InputConverter } from '../../../decorators';
import { OListItemComponent } from '../list-item/o-list-item.component';
import { OListItemCardRenderer } from './o-list-item-card-renderer.class';

export const DEFAULT_INPUTS_O_LIST_ITEM_CARD_IMAGE = [
  ...OListItemCardRenderer.DEFAULT_INPUTS_O_CARD_RENDERER,
  'avatar'
];

export const DEFAULT_OUTPUTS_O_LIST_ITEM_CARD_IMAGE = [
  ...OListItemCardRenderer.DEFAULT_OUTPUTS_O_CARD_RENDERER
];

@Component({
  moduleId: module.id,
  selector: 'o-list-item-card-image',
  template: `
    <mat-card fxLayout="column" fxLayoutAlign="center center" class="o-item-card" [class.small]="compareListHeight('small')"
      [class.medium]="compareListHeight('medium')" [class.large]="compareListHeight('large')">

      <mat-card-header *ngIf="avatar !== undefined">
        <img matCardAvatar src="{{ avatar }}">
        <mat-card-title *ngIf="title !== undefined"> {{ title }}</mat-card-title>
        <mat-card-subtitle *ngIf="subtitle !== undefined"> {{ subtitle }}</mat-card-subtitle>
      </mat-card-header>

      <img *ngIf="image !== undefined" matCardImage src="{{ image }}" [class.exists-action-button]="icon !== undefined">

      <button type="button" *ngIf="icon !== undefined && compareListHeight('small')" mat-mini-fab (click)="onActionIconClick($event)" class="action-button">
        <mat-icon>{{ icon }}</mat-icon>
      </button>

      <button type="button" *ngIf="icon !== undefined && (compareListHeight('medium') || compareListHeight('large'))" mat-fab (click)="onActionIconClick($event)"
        class="action-button">
        <mat-icon>{{ icon }}</mat-icon>
      </button>

      <mat-card-title *ngIf="avatar === undefined && title !== undefined"> {{ title }}</mat-card-title>
      <mat-card-subtitle *ngIf="avatar === undefined && subtitle !== undefined"> {{ subtitle }}</mat-card-subtitle>

      <mat-card-content *ngIf="!collapsible">
        <p>
          {{ content }}
        </p>
      </mat-card-content>

      <mat-card-actions>
        <button type="button" mat-button *ngIf="action1Text !== undefined" (click)="onAction1ButtonClick($event)">{{ action1Text }}</button>
        <button type="button" mat-button *ngIf="action2Text !== undefined" (click)="onAction2ButtonClick($event)">{{ action2Text }}</button>

        <div class="collapse-button-container">
          <button type="button" mat-icon-button *ngIf="collapsible" (click)="collapsed = !collapsed" class="collapse-button">
            <mat-icon *ngIf="collapsed" svgIcon="ontimize:keyboard_arrow_down"></mat-icon>
            <mat-icon *ngIf="!collapsed" svgIcon="ontimize:keyboard_arrow_up"></mat-icon>
          </button>
        </div>
      </mat-card-actions>

      <mat-card-content *ngIf="collapsible && !collapsed">
        <p>
          {{ content }}
        </p>
      </mat-card-content>

    </mat-card>
  `,
  styles: [`
    .mat-list .mat-list-item.o-card-item,.mat-list .mat-list-item.o-card-item .mat-list-item-content{height:auto}.o-list-item-card-image{padding:8px 0}.o-list-item-card-image .mat-card mat-card-actions,.o-list-item-card-image .mat-card mat-card-header,.o-list-item-card-image .mat-card mat-card-subtitle,.o-list-item-card-image .mat-card mat-card-title{width:100%}.o-list-item-card-image .mat-card.large{width:100%}.o-list-item-card-image .mat-card.medium{width:80%;margin-left:auto;margin-right:auto}.o-list-item-card-image .mat-card.small{width:60%;margin-left:auto;margin-right:auto}.o-list-item-card-image .mat-card.small img.exists-action-button{margin-bottom:-20px}.o-list-item-card-image .mat-card:not(.small):not(.medium):not(.large){width:100%}.o-list-item-card-image .mat-card .action-button{margin-left:auto}.o-list-item-card-image .mat-card img.exists-action-button{margin-bottom:-28px}.o-list-item-card-image .mat-card .collapse-button-container{-webkit-flex:1 1 auto;-ms-flex:1 1 auto;flex:1 1 auto;position:relative}.o-list-item-card-image .mat-card .collapse-button-container .collapse-button{margin-left:calc(100% - 40px)}
  `],
  inputs: [
    ...DEFAULT_INPUTS_O_LIST_ITEM_CARD_IMAGE,
    'content',
    'avatar',
    'icon',
    'collapsible',
    'collapsed'
  ],
  outputs: [
    ...DEFAULT_OUTPUTS_O_LIST_ITEM_CARD_IMAGE,
    'onIconClick : icon-action'
  ],
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-custom-list-item]': 'true',
    '[class.o-list-item-card-image]': 'true'
  }
})
export class OListItemCardImageComponent extends OListItemCardRenderer {

  protected _content: string;
  protected _avatar: string;
  protected _icon: string;
  @InputConverter()
  protected _collapsible: boolean = false;
  @InputConverter()
  protected _collapsed: boolean = true;

  onIconClick: EventEmitter<Object> = new EventEmitter<Object>();

  constructor(
    elRef: ElementRef,
    _renderer: Renderer2,
    _injector: Injector,
    @Optional() @Inject(forwardRef(() => OListItemComponent)) _listItem: OListItemComponent
  ) {
    super(elRef, _renderer, _injector, _listItem);
  }

  ngAfterViewInit() {
    this.modifyMatListItemElement();
  }

  onActionIconClick(e?: Event) {
    if (Util.isDefined(e)) {
      e.stopPropagation();
    }
    this.onIconClick.emit(event);
  }

  get content(): string {
    return this._content;
  }

  set content(val: string) {
    this._content = val;
  }

  get avatar(): string {
    return this._avatar;
  }

  set avatar(val: string) {
    this._avatar = val;
  }

  get icon(): string {
    return this._icon;
  }

  set icon(val: string) {
    this._icon = val;
  }

  get collapsible(): boolean {
    return this._collapsible;
  }

  set collapsible(val: boolean) {
    this._collapsible = val;
  }

  get collapsed(): boolean {
    return this._collapsed;
  }

  set collapsed(val: boolean) {
    this._collapsed = val;
  }

}

@NgModule({
  declarations: [OListItemCardImageComponent],
  imports: [CommonModule, OSharedModule],
  exports: [OListItemCardImageComponent]
})
export class OListItemCardImageModule { }
